<script>
import Givenword from './components/givenword/index'
import Retrivedword from './components/retrivedword/index'
import FooterCom from './components/footer/index'
export default {
  name: 'app',
  components: {
    Givenword,Retrivedword,FooterCom
  },

  data(){
    return{

      datareturned:""

    }
  },
  methods:{
    GivemeSomeTranslate(text,lang){

       event.preventDefault();

         this.$http.get("https://translate.yandex.net/api/v1.5/tr.json/translate?key=trnsl.1.1.20180830T032430Z.f083cc8e4265220a.cfe70e5a6083c7275c03417d20ca20c5f03c7141&text="+text+"&lang="+lang).then(response => {
           this.datareturned=response.body.text[0]
        }, response => {
          // error callback
        });
    }
  }
}
</script>


