<template>


          <div class="inoutsbox-textarea">
                    <select v-model="selected">
                         <option
    v-for="(lang, index) in langs" :key="index"
    
     v-bind:value="index">{{lang}}</option>
                </select>
                                                                       <textarea name="" id="" cols="30" rows="10" v-model="givenword" v-on:keyup="Blurme" ></textarea>

                                                                     <p>Type a word or text then select a language and use the <span>
                                                                                <img width="15px" src="https://upload.wikimedia.org/wikipedia/commons/f/f4/Oxygen480-actions-key-enter.svg" alt=""></span> Enter Key to SUBMIT
                         </p>  
                                                                </div>

                              



      


   
</template>


<script>
export default {
    name:"Givenword",

    data(){
    
          return{
                givenword:'',
                langs:'',
                selected:'fr'
          } 
     
    },

    created(){

         this.$http.get("https://translate.yandex.net/api/v1.5/tr.json/getLangs?key=trnsl.1.1.20180830T032430Z.f083cc8e4265220a.cfe70e5a6083c7275c03417d20ca20c5f03c7141&ui=JSON").then(response => {
          this.langs=response.body.langs
        }, response => {
          // error callback
        });
        
    },

    methods:{

        Blurme(event){
                if (event.keyCode==13 && !event.shiftKey) {
                     event.preventDefault();

                      this.$emit('translatme', this.givenword,this.selected)
                        return false;  
                }
        }

    }
}
</script>
